<template>
  <Navbar @searchValue="search = $event"/>
  <Notes 
  :notes="filterNotes" 
  @delNote="delNote"
  @changeNote="changeNote"
  :search="search"
  />
  <Modal 
  v-show="modalOpen" 
  @closeModal="closeModal" 
  @addNote="addNote"
  :edit="edit"
  :editNote="editNote"
  @editedNote="editedNote"
  />
  <AddButton @openModal="openModal"/>
</template>

<script>

import Navbar from '@/components/Navbar.vue'
import Notes from '@/components/Notes.vue'
import Modal from '@/components/Modal.vue'
import AddButton from '@/components/AddButton.vue'

export default {
  components: {
    Navbar,
    Notes,
    Modal,
    AddButton
  },
  data(){
    return {
      modalOpen: false,
      notes: [],
      edit: false,
      editNote: {},
      search: '',
    }
  },
  watch: {
    notes: {
      handler(newNotes){
        localStorage.notes = JSON.stringify(newNotes);
      },
      deep: true
    }
  },
  computed: {
    filterNotes(){
      return this.search ? this.notes.filter(note => note.title.toLowerCase().trim().includes(this.search.toLowerCase().trim())) : this.notes;
    }
  },
  created(){
    this.getNotes()
  },
  methods: {
    openModal(){
      this.modalOpen = true;
      this.edit = false;
    },
    closeModal(status){
      this.modalOpen = status;
    },
    addNote(note){
      this.notes.push(note)
      this.modalOpen = false;
    },
    delNote(id){
      let index = this.notes.findIndex(note => note.id == id);
      this.notes.splice(index, 1);
    },
    getNotes(){
      let localNotes = localStorage.notes
      if(localNotes){
        this.notes = JSON.parse(localNotes)
      }
    },
    changeNote(id){
      this.modalOpen = this.edit = true;
      let pickedNote = this.notes.find(note => note.id == id)
      this.editNote = pickedNote
    },
    editedNote(noteEdited){
      this.notes.forEach(note => {
        if(noteEdited.id == note.id){
          note.title = noteEdited.title;
          note.desc = noteEdited.desc;
          note.date = noteEdited.date;
        }
      })
    }
  }
}
</script>

